div.indentedBox {
    height: 275px;
    width: 200px;
    background: #e6e6e6; /* kolor tła boxu - najlepiej ustawić ciemniejszy od tła (lepszy efekt) */
    box-shadow: inset 2px 2px 10px 0px rgb(41, 41, 41); /* cień do środka */
    border-bottom: 1px solid #4b4b4b; /* obramowanie dolne */
    border-right: 1px solid #4b4b4b; /* obramowanie prawe */
    border-radius: 25px;
    margin-left: 30px;
}

div.centered {
    height: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageCentre {
    align-items: center;
    vertical-align: middle;
}

.projectCards {
    width: 60vw;
}

.projectCardsMobile {
    justify-content: center;
    margin-left: 25%;
    margin-right: 25%;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 50%;
}

.projectCardsMobileM {
    width: 50%;
    margin-left: 10%;
    margin-right: 10%;
}
